'use strict';
var querystring = require('querystring');
var client = require('../../../client');
var validate = require('../../../utils/validate');
var checkers = require('../../../utils/checkers');

/**
 * Execute deployment filter by filterId or filter condition
 * @param {object} params
 * @param {number} page
 * @param {number} limit
 * @param {string} sortBy
 * @param {string} sortOrder asc, desc
 * @param {function} callback optional
 */
module.exports = function (params, page, limit, sortBy, sortOrder, callback) {
  if (checkers.isFunction(page)) {
    callback = page;
    page = null;
  }
  if (checkers.isFunction(limit)) {
    callback = limit;
    limit = null;
  }
  if (checkers.isFunction(sortBy)) {
    callback = sortBy;
    sortBy = null;
  }
  if (checkers.isFunction(sortOrder)) {
    callback = sortOrder;
    sortOrder = null;
  }
  try {
    const query = {};
    if (page !== undefined && page !== null) {
      query.page = page;
      validate.validateNumber(page);
    }
    if (limit !== undefined && limit !== null) {
      query.limit = limit;
      validate.validateNumber(limit);
    }
    if (sortBy) {
      query.sortBy = sortBy;
      validate.validateString(sortBy);
    }
    if (sortOrder) {
      query.sortOrder = sortOrder;
      validate.validateString(sortOrder);
    }
    validate.validateObject(params);
    var path = '/deployments/filters/devices?' + querystring.stringify(query);
    return client.post('auth', path, params, callback);
  } catch (e) {
    if (callback) {
      return callback(e, null);
    }
    return Promise.reject(e);
  }
}